    font-family: "montserratmedium";
    color: #8c8c8c;
    font-size: 16px;
    text-transform: uppercase;
    margin-left: -15px;
    margin-right: -15px;
    margin-bottom: 15px;
    padding: 14px 15px 10px;
    line-height: 21px;
    border-bottom: 1px solid #ebebeb; }
  .b-schedule__timetable {
    font-size: 11px;
    line-height: 14px;
    text-transform: none;
    font-family: "montserratlight"; }



