.b-slider-schedule {
  position: relative; }
  .b-slider-schedule__header {
    height: 30px;
    margin-bottom: 15px;
    padding: 0px 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid;
    border-top: 1px solid;
    border-color: #ebebeb; }
  .b-slider-schedule__title {
    font-family: "montserratregular";
    font-size: 14px;
    line-height: 17px;
    padding-top: 2px; }
  .b-slider-schedule__slide:focus {
    outline: none; }

.tns-outer .tns-controls {
  margin-bottom: -30px;
  height: 30px;
  position: relative;
  z-index: 1; }



