.b-schedule-item {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  cursor: pointer; }
  .b-schedule-item__time-wrap {
    line-height: 32px;
    display: inline-block;
    flex-shrink: 0;
    margin-right: 35px;
    padding-top: 10px; }
  .b-schedule-item__time {
    color: #222222;
    font-family: "montserratmedium";
    font-size: 14px;
    line-height: 21px; }
    .b-schedule-item__time--finish {
      color: #cccccc; }
  .b-schedule-item__info {
    display: inline-block;
    width: 100%;
    border-bottom: 1px solid #ebebeb;
    padding-top: 5px;
    padding-bottom: 10px;
    min-height: 115px; }
  .b-schedule-item__state {
