.b-page-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  -webkit-overflow-scrolling: touch;
  overflow-x: hidden;
  overflow-y: auto;
  height: 100%;
  min-width: 320px; }
  .b-page-wrapper.no-scroll {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden; }
  .b-page-wrapper.no-touch {
    pointer-events: none;
    touch-action: none; }



